import React, { useEffect } from 'react';
import styled from 'styled-components';
import PagePreloader from '@components/preloader/PagePreloader';
import SojiBaseInit from '@modules/SojiBase';
import { useNavigate } from 'react-router-dom';

const Container = styled.div`
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
`;

export default function RequestAction({ state }) {
    const navigate = useNavigate();
    useEffect(() => {
        (async () => {
            const payload = {
                document: state.userTextToScan,
                name: state.name,
                email: state.email,
                phoneNumber: state.phoneNumber,
                socialMediaLink: state.socialMediaLink,
                scamSource: state.scamSource,
                alreadyScammed: state.alreadyScammed == "" ? "No" : "Yes",
                scammedAmount: state.scammedAmount,
            };
            const SojiBase = await SojiBaseInit;
            const requestPayload1 = { userText: payload.document };
            const getter = await SojiBase.post('https://soji-app.herokuapp.com/api/v1/predict1', requestPayload1, true);
            getter.on('static', async (response) => {
                const predictionData = response.percentage;
                if (predictionData) {
                    payload.prediction = predictionData;
                    const getter = await SojiBase.post('v1/suspected_users/save_seaches', payload);
                    getter.on('static', (response) => {
                        if (response.statusCode == 200) {
                            const data = response.response.data;
                            navigate(`/get-prediction-results/${data.uniqueId}`, { replace: false });
                        }
                    });
                }
            });
        })();
    }, []);

    return (
        <Container>
            <PagePreloader />
        </Container>
    );
}
